body {
  color: #4d4d4d;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

button, input {
  font: inherit;
}

header {
  max-width: 640px;
  text-align: center;
  margin: 1rem auto 0;
  font-size: small;
}

header * {
  font-size: 1rem;
  display: inline;
}

#game {
  --card-size: 18vh;
  grid-template-columns: repeat(var(--columns), var(--card-size));
  justify-content: center;
  gap: 2vh;
  padding: 2vh;
  display: grid;
}

[data-type="card"] {
  --background-color-lightness: 90%;
  background-color: hsl(220, 20%, var(--background-color-lightness));
  color: #0000;
  cursor: pointer;
  height: var(--card-size);
  border: 1vh solid #fff;
  border-radius: 2vh;
  font-size: 5vh;
  position: relative;
  box-shadow: 0 2px 4px #0006;
}

[data-type="card"]:focus, [data-type="card"]:hover {
  transform: scale(1.025);
  box-shadow: 0 1px 6px #0000004d;
}

[data-type="card"]:after {
  color: #00000080;
  content: attr(data-key);
  font-size: 3vh;
  position: absolute;
  bottom: 1vh;
  right: 1vh;
}

[data-type="card"][data-state] {
  color: initial;
}

[data-type="card"][data-state][data-state="active"] {
  --background-color-lightness: 94%;
  transform: scale(1.05);
  box-shadow: 0 5px 10px #0003, 0 10px 20px #0000001a;
}

[data-type="card"][data-state][data-state="done"] {
  background-color: #c9e8c9;
}

[data-type="card"][data-state]:after {
  opacity: .25;
}

